.cp_reg-line {
  width: 100%;
  height: 12px;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  border-radius: 2px;
  position: relative;
}

.cp_reg-line.color {
  background: -webkit-gradient(
    linear,
    right top, left top,
    from(#f00),
    color-stop(16%, #ff0),
    color-stop(33%, #0f0),
    color-stop(50%, #0ff),
    color-stop(66%, #00f),
    color-stop(83%, #f0f),
    to(#f00)
  );
  background: linear-gradient(
    to left,
    #f00 0%,
    #ff0 16%,
    #0f0 33%,
    #0ff 50%,
    #00f 66%,
    #f0f 83%,
    #f00 100%
  );
}
.cp_reg-line.opacity {
