}

.cp_copy-color {
  position: relative;
  margin-left: 10px;
  cursor: pointer;
  display: flex;
  align-items: center;
}
.cp_copy-color:before {
  position: absolute;
  opacity: 0;
  z-index: 1000000;
  pointer-events: none;
  content: '';
  left: 47%;
  top: -8px;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  background: transparent;
  border: 6px solid transparent;
  border-top-color: #38486d;
  z-index: 1000001;
  -webkit-transition: 0.3s;
  transition: 0.3s;
}

.cp_copy-color[name]:after {
  position: absolute;
  opacity: 0;
  z-index: 1000000;
  pointer-events: none;
  bottom: calc(100% + 8px);
  left: 50%;
  -webkit-transform: translate(-50%, 0);
          transform: translate(-50%, 0);
  background: #38486d;
  border-radius: 3px;
  color: white;
  padding: 7px 8px;
  font-size: 12px;
  font-family: Arial, sans-serif;
  line-height: 11px;
  white-space: nowrap;
  content: attr(name);
  -webkit-transition: 0.3s;
  transition: 0.3s;
}

.cp_copy-color.active:before,
.cp_copy-color.active:after {
  opacity: 1;
  -webkit-transition: 0.3s;
  transition: 0.3s;
}

.cp_copy-color img {
  max-width: 20px;
}

