/* Enter , appear */
.warning-enter,
.warning-appear {
  opacity: 0.7;
}
.warning-enter:before,
.warning-appear:before {
  -webkit-transform: scale(0);
          transform: scale(0);
  opacity: 0;
}

.warning-enter-active:before,
.warning-appear-active:before {
  -webkit-transform: scale(1);
          transform: scale(1);
  opacity: 1;
  -webkit-transition: 0.3s;
  transition: 0.3s;
}

.warning-enter-done:before,
.warning-appear-done:before {
  -webkit-transform: scale(1);
          transform: scale(1);
  opacity: 1;
}

.warning-enter-active,
.warning-appear-active {
  opacity: 1;
}

.warning-exit {
  opacity: 1;
}
.warning-exit:before {
  -webkit-transform: scale(1);
          transform: scale(1);
  opacity: 1;
}
.warning-exit-active {
  opacity: 0;
  -webkit-transition: 0.3s;
  transition: 0.3s;
}
.warning-exit-active:before {
  -webkit-transform: scale(0);
          transform: scale(0);
  opacity: 0;
  -webkit-transition: 0.3s;
  transition: 0.3s;
}

