/* Animation */
.example-enter,
.example-appear {
  opacity: 0.7;
}

.example-enter-active,
.example-appear-active {
  opacity: 1;
  -webkit-transition: opacity 300ms;
  transition: opacity 300ms;
}

.example-exit {
  opacity: 1;
  -webkit-transition: opacity 300ms;
  transition: opacity 300ms;
}
/* fires as element leaves the DOM*/
.example-exit-active {
  opacity: 0.7;
}

